<template>
  <div>
    <v-data-table
      :headers="headers"
      :items="desserts"
      class="elevation-1 mt-3 rounded-lg"
    >
      <template slot="item.index" slot-scope="{ item }">
        <b>{{ getIndex(item) }}</b>
      </template>
    </v-data-table>
  </div>
</template>
    
    <script>
export default {
  name: "TableStudents",
  props: ["idCycle"],
  data() {
    return {
      headers: [
        {
          text: "#",
          align: "start",
          sortable: false,
          value: "index",
        },
        { text: "Nombre", value: "person.name" },
        { text: "Paterno", value: "person.lastnameFather" },
        { text: "Materno", value: "person.lastnameMother" },
        { text: "DNI", value: "person.dni" },
        { text: "Celular", value: "person.phone" },
        { text: "Correo", value: "person.email" },
      ],
      desserts: [],
    };
  },
  created() {},
  methods: {
    async loadTable(idSpecialty) {
      await this.$axios
        .get(
          "/academic-cicle/" +
            this.idCycle +
            "/speciality/" +
            idSpecialty +
            "/enroll?perPage=100",
          {
            headers: {
              Authorization: `Bearer ${localStorage.token}`,
            },
          }
        )
        .then((response) => {
          this.desserts = response.data.allItems;
        })
        .catch((error) => {
          this.$queryError(error);
          this.$toast.error(error.response.data.content);
        });
    },
    // Limpiar los datos de la tabla
    clearTable() {
      this.desserts = [];
    },
    getIndex(item) {
      // Obtener el índice del elemento en la lista
      const index = this.desserts.indexOf(item);
      // Agregar 1 al índice para obtener el número correspondiente
      return index + 1;
    },
  },
};
</script>